import { View } from '../../framework/views/View';
import { User, UserProps } from '../models/User';

export class UserShow extends View<User, UserProps> {
  template(): string {
    return `
    <h3>User Detail</h3>
    name: ${this.model.get('name')}
    <br>
    age: ${this.model.get('age')}
    <br>
    <br>
    `;
  }
}
